
body.show-modal .backdrop {
    opacity: 1;
    visibility: visible;
    pointer-events: initial;
}
body.show-modal .modal {
    transform: scale(1);
}


.backdrop {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 999;

    pointer-events: none;
    opacity: 0;
    visibility: hidden;
    transition: opacity 200ms ease-in-out, visibility 200ms ease-in-out;
}

.modal-command {
    max-width: 265px;
    padding: 24px;
    border-radius: 4px;
    width: 100vw;
    min-height: 320px;
    background-color: #fff;
    box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 8px 2px 1px -1px rgba(0,0,0,0.12);
    transform: scale(1);
    transition: transform 280ms ease-in-out;
    

    @include tab() {
        max-width: 675px;
    }


    @include desk() {
        max-width: 1125px;
}

}